@import url(https://fonts.googleapis.com/css2?family=Copse&family=Oxygen:wght@300;400&family=Viaoda+Libre&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Copse&family=Oxygen:wght@300&family=Viaoda+Libre&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  font-size: 16px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  height: 100vh;
  width: 100vw;
}

header{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    width: 100vw;
    height: 3em;
    background-color: #f321d7;
    position: absolute;
    z-index: 100;
}

.header__img--1{
    margin-left: 3%;
    margin-right: 3%;
}
footer{
    width: 100vw;
    height: 2.5em;
    background-color: #f321d7;
    margin-bottom: 0px;
    bottom: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    padding: .6rem;
    z-index: 100;
    position:fixed;
}
.menu__main--container{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.menu__title{
    margin-top: 4.5rem;
    font-size: 2rem;
    font-family: 'Copse', serif;
}

.menu__container{
    display: flex;
    flex-direction: row;
    display: inline block;
    margin-top:5%;
    margin-left: 4%;
    height: 28rem;
    font-family: 'Oxygen', sans-serif;
}


.menu__container--options{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    margin-top:0;
    margin: 2rem;
    height: 25rem;
    width: 30rem;
}

.option{
    margin-top: 12%;
    height: 20%;
    width: 40%;
    text-align: center;
    font-size: 1.5rem;
    padding: 5%;
    border: none;
    border-radius: 12px;
    background: linear-gradient(-45deg, #053553, #165983, #3482b3, #7bc2ee);
    background-size: 150% 150%;
    font-family: 'Oxygen', sans-serif;
}

.instructions__container{
    height: 22rem;
    width: 16rem;
    padding: 8%;
    margin-top: 12%;
    border: 1px solid #053553;
}

.instructions__container--title{
    margin-top: 1%;
    margin-bottom: 10%;
    text-align: center;
}

.instructions__container--subtitle{
    margin-bottom: 10%;
}

li{
    list-style: lower-roman;
    text-align: justify;
}

@media screen and (max-width: 400px) {
    .menu__container{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
}
.cashregister__main__container{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    overflow-x: hidden;
}

.display__container{
    display: flex;
    justify-content: center;
    height: 4.2rem;
    width: 9rem;
    background-color: #000000;
    margin-top: 6.5rem;
    padding: .05%;
    border-radius: .5%;
}

.display__container--qty{
    height: 3.3em;
    width: 7.6em;
    margin-top: .4rem;
    text-align: end;
    font-size: 16px;
    border-radius: 3%;
    background-color: #e7edee;
    box-shadow: inset -2px -2px 7px #ffffff,
    inset 3px 3px 6px rgba(0, 0, 0, 0.4);
    padding: 4%;
}

.cashregister__container{
    background: linear-gradient(-45deg, #053553, #165983, #3482b3, #7bc2ee);
    background-size: 100% 100%;
    height: 13rem;
    width: 25rem;
    margin-top: 10.7rem;
    margin-left: -25rem;
    border-radius: .5%;
    box-shadow: inset -5px -5px 12px #165b86, 
    inset 5px 5px 12px rgba(0, 0, 0, 0.16);
}

.ticket__container{
    display: flex;
    justify-content: center;
    height: 1rem;
    width: 6rem;
    margin-top: 1rem;
    margin-left: 2rem;
    background-color: #979797;
    box-shadow: inset -2px -2px 7px #858488,
    inset 2px 2px 4px rgba(0, 0, 0, 0.4);
}

.ticket__container--inside{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 6.5rem;
    width: 4.4rem;
    margin-top: -97%;
    background: linear-gradient(-240deg, #5bf1f1, #acf3f3, #f5f8f8);
    background-size: 100% 100%;
    border-radius:6%;
    opacity: 85%;
}

.ticket__item{
    height: .2em;
    width: 2.5em;
    background-color: #053553;
    opacity: 65%;
}

.ticket__item--total{
    font-size: .7rem;
    color:#053553;
}

.key__slot{
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    background-color:#979797;
    margin-left: 85%;
    border: 3px solid #cabdd4;
}

.btn__submit{
    width: 6rem;
    height: 1.8rem;
    margin-top: 25%;
    margin-left: 70%;
    border-radius: 25px;
    background-color: #24b430;
    border: none;
    padding: 5px;
    font-size: 1rem;
    font-family: 'Oxygen', sans-serif;
    color: #ffffff;
}

.cash__register--cash{
    height: 7rem;
    width: 25rem;
    margin-top: 23.7rem;
    margin-left: -25rem;
    background-color: #000000;
}

.cash__register--slot{
    display: flex;
    justify-content: center;
    align-items: center;    
    height: 6rem;
    width: 25rem;
    margin-top: 1em;
    background-color: #979797; 
}

.cash__register--slot--margin{
    height: 5rem;
    width: 23rem;
    background-color: transparent; 
    border: 5px solid #504c4c;
}

.cash__register--small--slot{
    margin-top: 9%;
    margin-left: 45%;
    height: .4em;
    width: 3em;
    background-color: #e7edee;
}

.buttons__container{
    display: flex;
    flex-flow: row wrap;
    margin-top: -36%;
    height: 9rem;
    width: 9.5rem;
    margin-left:17%;
}

.button__item{
  width: 1.8rem;
  height: 1.8rem;
  margin-bottom: .6rem;
  margin-right: 1.3rem;
  border-radius: 50%;
  font-size: 1rem;
  background-color: #e7edee;
  box-shadow: -1px -1px 6px #ffffff, 
  1px 1px 6px rgba(0, 0, 0, 0.16);
  border: none;
}
